export default {
	data() {
		return {
			roomIndex:0,
			ongoing_info:'',
			wc_order:""
		}
	},
	onLoad(e) {
		var tokes = uni.getStorageSync("token")
		var uid = tokes.uid
		this.addree()
	},
	onPullDownRefresh() {
	    this.refresh()
	},
	onShow() {
		this.orderIndex()
		// this.addree()
	},
	methods: {
		async orderIndex(uid){
			uni.showLoading({})
			const that = this
			var tokes = uni.getStorageSync("token")
			var uid = tokes.uid
			const res = await this.$myRequest({
				method:"POST",
				url:"/orders?uid=" + uid
			})
			uni.hideLoading()
		    that.ongoing_info = res.data.data.ongoing_info
			that.wc_order = res.data.data.wc_order
		},
		quitIndex(id){
			uni.navigateTo({
				url:"../drawback/drawback?id=" + id
			})
	    },
		continueIndex(id){
			uni.navigateTo({
				url:"../continue_pay_dues/continue_pay_dues?id="+ id
			})
		},
		addree(){
		    const token = uni.getStorageSync("token")
			uni.showLoading({})
			if(!token){
				uni.navigateTo({
					url:"../login/login"
				})
				return
			}
			uni.hideLoading()
		},
		refresh(){
			setTimeout(()=>{
				this.orderIndex()
				uni.stopPullDownRefresh()
			},2000)
		},
		kaiSuo(id){
			const oid = id
			const that = this
			uni.showModal({
				title:"确定开锁吗",
				success(res) {
					if(res.confirm){
						that.kaiSuoIndex(oid)
					}else{
						
					}
				}
			})
		},
		async kaiSuoIndex(id){
			const that = this
			const res = await this.$myRequest({
				method:"POST",
				url:"/openLock",
				data:{
					oid:id
				}
			})
			uni.showToast({
				title:res.data.msg
			}) 
		}
	}
}
